body {
  font-family: "El Messiri", Roboto, sans-serif;
  font-size: 18px;
  line-height: 1.5;
  font-weight: 400;
  color: #fff;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  display: flex;
  min-height: 100%; }

input,
textarea,
select {
  vertical-align: middle;
  font-family: "El Messiri", Roboto, sans-serif;
  font-size: 18px;
  line-height: 1.5;
  font-weight: 400;
  color: #fff; }

button,
input[type="button"],
input[type="reset"],
input[type="submit"] {
  -webkit-appearance: none;
  cursor: pointer;
  transition: .3s all; }

button::-moz-focus-inner,
input[type="reset"]::-moz-focus-inner,
input[type="button"]::-moz-focus-inner,
input[type="submit"]::-moz-focus-inner {
  border: none;
  padding: 0; }

a:focus,
button:focus,
input:focus,
textarea:focus,
select:focus {
  outline: none; }

input[type="search"] {
  -webkit-appearance: none; }

input {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none; }

input[type="text"] {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none; }

input[type="email"] {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none; }

input[type="password"] {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none; }

table {
  border-collapse: collapse; }

*,
*:before,
*:after {
  box-sizing: border-box; }

p {
  margin: 0 0 16px; }

