// Burger
.burger {
  height: 34px;
  width: 34px;
  display: none;

  @media only screen and (max-width: 1023px) {
    display: block;
    position: absolute;
    top: 50%;
    right: 10px;
    transform: translateY(-50%);
  }

  &__holder {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    width: 100%;
  }

  &._is-active {
    .line {
      background-color: transparent;

      &:after,
      &:before {
        bottom: 0;
      }

      &:after {
        transform: rotate(45deg);
      }

      &:before {
        transform: rotate(-45deg);
      }
    }
  }
}

.line {
  display: inline-block;
  width: 100%;
  height: 2px;
  background-color: $hover-color;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,- 50%);
  transition: 0.2s ease;

  &:after,
  &:before {
    content: '';
    position: absolute;
    left: 0;
    right: 0;
    width: 100%;
    height: 2px;
    background-color: $hover-color;
    transition: 0.2s ease;
    transform: rotate(0);
    transform-origin: center;
  }

  &:after {
    bottom: -8px;
  }

  &:before {
    bottom: 8px;
  }
}